import {Observable, Subscriber} from 'rxjs';

export function checkURL(url: string): Observable<void> {
  const img = new Image();

  img.src = url;

  return new Observable((observer: Subscriber<void>) => {
    img.onload = () => {
      observer.next();
      observer.complete();
    };

    img.onerror = () => {
      observer.error();
    };
  });
}
